import assign from '../utils/assign'

const defaults = {
  title: '',
  confirmActionText: '',
  cancelActionText: '',
  promptPlaceholder: '',
  reverseActions: false,
  closeButton: '',
  preventCancel: true,
}

function setDefaults(options, value) {
  if (arguments.length === 2) {
    options = {
      [options]: value,
    }
  }

  return assign(defaults, options)
}

export {setDefaults}

export default defaults
